.App {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  flex-direction: column; /* Stack children vertically */
  margin: 0; /* Remove default margin */
}

.App-header {
  background-color: #7B7EE6;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%; /* Ensures the header takes full width */
}

.App-header h1 {
  margin: 0;
  font-size: 16px;
  color: white;
}

.nav-links {
  display: flex;
  gap: 15px;
  font-size: 16px;
  color: white;
}

.Search-section {
  text-align: center;
  flex-grow: 1; /* Take available space if needed */
}

.Search-section h2 {
  text-transform: uppercase;
  margin-top: 200px;
}

.search-input {
  padding: 5px;
  font-size: 16px;
}

.search-button {
  padding: 5px 10px;
  font-size: 16px;
  cursor: pointer;
}
